
.register {
  position: relative;
  left: 30%;
  top: 10em;
  width: 40%;
  font-family: Arial;
}
.register .register-form {
  margin: 0 auto;
  width: 80%;
  font-size: 18px;
}
.register .register-form .form-group {
  padding-top: 10px;
  height: 60px;
}
.register .register-form .form-group .label-info {
  width: 45%;
  height: 60px;
  line-height: 60px;
  text-align: right;
  font-weight: bold;
}
.register .register-form .form-group .input-info {
  width: 55%;
  height: 35px;
  border-radius: 4px;
  font-size: 18px;
  outline: none;
}
.register .register-form .form-group .check {
  color: #f00;
}
.register .register-form .form-control {
  text-align: right;
  margin-right: 62px;
}
.register .register-form .form-control .button {
  width: 100px;
  height: 30px;
  border-radius: 5px;
}


